.todo {
    position: relative;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    width: 260px;
    height: 246px;
    background-color: #fff;
    margin: 20px;
    padding: 15px 20px 30px;
    border: 2px solid #000;
    font-weight: 600;
    transition-duration: 0.5s;
}

.todo__check {
    position: absolute;
    top: -14px;
    right: -2px;
    width: 25px;
    background-color: #fff;
    border: 2px solid #000;
    text-align: center;
    transition: all 0.3s;
    cursor: pointer;
}

.todo__title {
    font-size: 18px;
}

.todo__description {
    margin-top: 50px;
}

.todo__footer {
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
}

.done {
    background-color: #e5e5e5;
}

.todo__button {
    position: relative;
    width: 20px;
    height: 20px;
    padding: 10px;
    background-color: transparent;
    font-weight: bold;
    font-size: 15px;
    text-align: center;
}

.todo__menu {
    position: relative;
    left: -68px;
    top: 27px;
    width: 100px;
}