.form-wrapper {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(246,246,246,0.8);
    z-index: 1;
}

.form {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 319px;
    width: 250px;
    margin-top: -40px;
    padding: 20px;
    border: 2px solid #000;
    font-size: 20px;
    background-color: #fff;
}

label {
    font-size: 18px;
    padding-left: 3px;
}

.title_input {
    height: 25px;
}

.description_input {
    height: 55px;
}

.change-priority {
    height: 25px;
}

.form__footer {
    display: flex;
    justify-content: space-around;
    width: 100%;
    margin-top: 70px;
}

.form button {
    padding: 5px 17px;
}
